._exradicated-tooltip {
  position: absolute;
  display: block;
  opacity: 1;
  z-index: 999;
}

tooltip {
  display: inline-block;
  position: relative;
  z-index: 10002;
}

@-webkit-keyframes animate-tooltip {
  0% {
    opacity: 0;
  }
  50% {
    opacity: .5;
  }
  60% {
    opacity: .8;
  }
  70% {
    opacity: .9;
  }
  90% {
    opacity: 1;
  }
}

@-moz-keyframes animate-tooltip {
  0% {
    opacity: 0;
  }
  50% {
    opacity: .5;
  }
  60% {
    opacity: .8;
  }
  70% {
    opacity: .9;
  }
  90% {
    opacity: 1;
  }
}

@-ms-keyframes animate-tooltip {
  tooltip 0% {
    opacity: 0;
  }
  tooltip 50% {
    opacity: .5;
  }

  tooltip 60% {
    opacity: .8;
  }

  tooltip 70% {
    opacity: .9;
  }

  tooltip 90% {
    opacity: 1;
  }
}

@keyframes animate-tooltip {
  0% {
    opacity: 0;
  }
  50% {
    opacity: .5;
  }
  60% {
    opacity: .8;
  }
  70% {
    opacity: .9;
  }
  90% {
    opacity: 1;
  }
}

tooltip._multiline {
  display: block;
}

tooltip._slow._ready tip {
  animation: animate-tooltip 0.65s;
}

tooltip._fast._ready tip {
  animation: animate-tooltip 0.15s;
}

tooltip._steady._ready tip {
  animation: animate-tooltip 0.35s;
}

tooltip tip {
  border-radius: 3px;
  background: #fff;
  color: rgba(0, 0, 0, .8);
  display: none;
  line-height: normal;
  max-width: 500px;
  min-width: 100px;
  opacity: 0;
  padding: 8px 16px;
  position: absolute;
  text-align: center;
  width: auto;
  will-change: top, left, bottom, right;
  -webkit-box-shadow: 0 0 1px rgba(57, 70, 78, .15), 0 10px 55px -8px rgba(57, 70, 78, .45);
  -moz-box-shadow: 0 0 1px rgba(57, 70, 78, .15), 0 10px 55px -8px rgba(57, 70, 78, .45);
  box-shadow: 0 0 1px rgba(57, 70, 78, .15), 0 10px 55px -8px rgba(57, 70, 78, .45);
}

tooltip tip._hidden {
  display: block;
  visibility: hidden;
}

tooltip.active tip {
  display: block;
  opacity: 1;
  z-index: 999;
}

tooltip tip-tip {
  font-size: .95em;
}

tooltip tip-tip._large {
  font-size: 1.1em;
}

tooltip tip-tip._small {
  font-size: .8em;
}

tooltip tip-tip #close-button {
  cursor: pointer;
  float: right;
  left: 8%;
  margin-top: -7%;
  position: relative;
}

tooltip._top tip {
  left: 50%;
  top: -9px;
  transform: translateX(-50%) translateY(-100%);
}

tooltip._top tip tip-arrow {
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid rgba(0, 0, 0, 0.1);
  content: '';
  height: 0;
  left: 50%;
  margin-left: -10px;
  position: absolute;
  top: 100%;
  width: 0;
}

tooltip._bottom tip {
  right: 50%;
  top: 100%;
  transform: translateY(9px) translateX(50%);
}

tooltip._bottom tip tip-arrow {
  border-bottom: 10px solid rgba(0, 0, 0, 0.1);
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  bottom: 100%;
  content: '';
  height: 0;
  left: 50%;
  margin-left: -10px;
  position: absolute;
  width: 0;
}

tooltip._right tip {
  left: 100%;
  top: 50%;
  transform: translateX(9px) translateY(-50%);
}

tooltip._right tip tip-arrow {
  border-bottom: 10px solid transparent;
  border-right: 10px solid rgba(0, 0, 0, 0.1);
  border-top: 10px solid transparent;
  content: '';
  height: 0;
  margin-top: -6px;
  position: absolute;
  right: 100%;
  top: 50%;
  width: 0;
}

tooltip._left tip {
  left: -9px;
  top: 50%;
  transform: translateX(-100%) translateY(-50%);
}

tooltip._left tip tip-arrow {
  border-bottom: 10px solid transparent;
  border-left: 10px solid rgba(0, 0, 0, 0.1);
  border-top: 10px solid transparent;
  content: '';
  height: 0;
  left: 100%;
  margin-top: -6px;
  position: absolute;
  top: 50%;
  width: 0;
}